import React, { FC } from 'react'
import clsx from 'clsx'
import { Typography } from '../../other/Typography'
import { AccentCardProps, CardTypoGraphyStyle } from './types'
import './AccentCard.scss'

const typeObj: CardTypoGraphyStyle = {
  S: 'Body2B',
  M: 'Body1B',
}

const AccentCard: FC<AccentCardProps> = ({
  className,
  isAccented = false,
  title,
  children,
  size = 'S',
  icons = [],
}) => {
  return (
    <div
      className={clsx(
        'AccentCard',
        className,
        clsx({
          'AccentCard_size-S': size === 'S',
          'AccentCard_size-M': size === 'M',
          AccentCard_isAccented: isAccented,
          'AccentCard_isAccented_size-M': isAccented && size === 'M',
        }),
      )}
    >
      <Typography
        className='AccentCard__Title'
        text={title}
        type={typeObj[size]}
      />
      {children}
      {icons.length > 0 && (
        <div className='AccentCard__Icon'>
          {icons.map(({ iconSrc, alt }) => (
            <img alt={alt} src={iconSrc} />
          ))}
        </div>
      )}
    </div>
  )
}

export default AccentCard
